@import url(https://fonts.googleapis.com/css2?family=Work+Sans:ital,wght@1,500&display=swap);
body {
	margin: 0;
	font-family: 'Work Sans';
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: #eceff1;
	color: #4d4d4d;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.Navbar {
	background: rgba(57, 57, 57, 1);
	width: 100%;
	padding: 1.5rem;
	display: flex;
	justify-content: space-around;
}

.Navbar a {
	text-decoration: none;
	color: rgba(163, 163, 163, 1);
	font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
	font-weight: 300;
	transition: all 0.5s ease;
}

.Navbar a:hover {
	text-decoration: underline;
}

.Navbar a.Navbar-active {
	color: white;
}

.Message {
	padding: 2rem;
	margin: 1rem auto;
	background-color: rgba(0, 0, 0, 0.4);
	width: 50%;
	color: white;
	text-transform: uppercase;
	border-radius: 10px;
	text-align: center;
}

.Home-img {
	width: 100%;
	border: 3px solid white;
	border-radius: 15px;
	margin: 0 auto;
}

.Pokecard {
	width: 225px;
	padding: 10px;
	margin: 1rem;
	box-shadow: 7px 10px 12px -5px rgba(0, 0, 0, 0.56);
	text-align: center;
	background-color: white;
	border-radius: 3px;
}

.Pokecard-data {
	font-size: 80%;
}

.Pokecard-title {
	margin-bottom: 1rem;
	font-weight: bold;
	font-size: 1.5rem;
}

.Pokecard-image {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 250px;
	background-color: #eceff1;
	border-radius: 3px;
	margin-bottom: 1rem;
}
.Pokecard img {
	transition: all ease 500ms;
}

.Pokecard img:hover {
	transform: scale(1.25);
	opacity: 0.8;
}

.Pokedex-cards {
	display: flex;
	justify-content: space-evenly;
	flex-wrap: wrap;
}

.Pokedex {
	text-align: center;
}

.Pokedex h2 {
	color: white;
	font-weight: 400;
}

.Pokedex h4 {
	color: white;
	font-weight: 400;
}

.Random {
	margin: auto;
	width: 50%;
}

.Random h2,
p,
form {
	text-align: center;
	color: white;
	font-weight: 400;
}

.Random button {
	margin-top: 10px;
	background-color: #008cba;
	color: white;
	padding: 15px 32px;
	text-align: center;
	text-decoration: none;
	display: inline-block;
	font-size: 16px;
	border: 2px solid gray;
	border-radius: 15px;
}

.Random button:hover {
	cursor: pointer;
}

body {
	background-image: repeating-linear-gradient(-45deg, #6a6a6a 0, #6a6a6a 2px, #727272 2px, #727272 11px);
}

